import React, { useState, useRef, useEffect } from 'react';
import { Link } from 'react-router-dom';

import Logo from '../assets/images/logo.svg';

function Header(props) {
    const [query, setQuery] = useState('');
    const timeout = useRef();

    const onInputChange = (evt) => {
        setQuery(evt.target.value);
        if (timeout.current) {
            clearTimeout(timeout.current);
        }
        timeout.current = setTimeout(function () {
            alert('hi');
        }, 500);
    };

    return (
        <header>
            <div className="container d-flex align-items-center justify-content-start">
                <Link to={'/'} title="Casset, Latest and Trend Musics" target="_self"><img src={Logo} alt="Casset" /></Link>
                <small>Enjoy music without borders!</small>
                <div className="search-wrapper">
                    <input type="text" placeholder="Looking for something?" onChange={onInputChange} value={query} />
                    <i className="fal fa-search"></i>
                </div>
            </div>
        </header>
    );
}

export default Header;