import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';

import MusicsContainer from '../../ui/musicsContainer';
import Vitrine from '../../ui/vitrine';
import * as Actions from '../../state/actions/home';
import Loading from '../../ui/loading';

function Home(props) {
    const { musicsFind, vitrines, fetchMusicsFind, fetchVitrines } = props;

    useEffect(() => {
        if (!musicsFind.data.length) {
            fetchMusicsFind();
        }
        if (!vitrines.data.length) {
            fetchVitrines();
        }
    }, []);

    return (
        <main className="container">
            {vitrines.loading ? <Loading loading={true} ></Loading> : vitrines.data.filter(vitrine => vitrine.type === 'hero_sliders').map(vitrine => <Vitrine hero loading={true} data={vitrine} />)}
            <MusicsContainer title="Discover New Music" musics={musicsFind.data} loading={musicsFind.loading} />
            {vitrines.loading ? <Loading loading={true} ></Loading> : vitrines.data.filter(vitrine => vitrine.type !== 'hero_sliders').map(vitrine => <Vitrine data={vitrine} />)}
        </main>
    );
}

const mapStateToProps = state => {
    return {
        musicsFind: state.homeReducer.musicsFind,
        vitrines: state.homeReducer.vitrines
    };
};

const mapDispatchToProps = dispatch => {
    return {
        fetchMusicsFind: () => dispatch(Actions.fetchMusicsFind()),
        fetchVitrines: () => dispatch(Actions.fetchVitrines())
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);