import Home from '../pages/home';
import Playlist from '../pages/playlist';
import Playlists from '../pages/playlists';

const Routes = [
    {
        routeName: 'home',
        component: Home,
        exact: true,
        path: '/'
    },
    {
        routeName: 'playlist',
        component: Playlist,
        exact: true,
        path: '/playlist/:id'
    },
    {
        routeName: 'playlists',
        component: Playlists,
        exact: true,
        path: '/playlists/:id'
    }
];

export default Routes;