import React, { useState } from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import Playlist from './playlist';

function PlaylistsContainer(props) {
    const { playlists, loading } = props;

    let settings = {
        dots: false,
        infinite: false,
        speed: 250,
        slidesToScroll: 2,
        variableWidth: true
    };

    return (
        <div className="musics-container playlists-container">
            <Slider {...settings}>
                {playlists.map((playlist, i) => <div className="slick-item-outer"><Playlist key={i} playlist={playlist} /></div>)}
            </Slider>
        </div>
    );
}

export default PlaylistsContainer;